import React , {useState} from 'react';
import'../App.css';

const ToDo = () => {
    const img = 'images/logo.jpg';
    const [inputData, setInputData] = useState('');
    const [items, setItems] = useState([]);
    const [toggle, setToggle] = useState(false);
    const [Id, setId] = useState(null);
    const addItem = () =>{
         if (inputData && toggle){
            setItems(
                items.map((element) =>{
                    if (element.id === Id){
                        return {...element ,name : inputData};
                    }
                    return element;
                })
            ); 
                setToggle(false);
            }
        else if(inputData){
            const data = {id : new Date().getTime().toString(), name : inputData};
            setItems([...items , data]);
        }
        else {
            alert('Please Enter Data');
        }
        setInputData('');
    };

    const deleteItem = (id) => {
        const updatedItems = items.filter((element , index) =>{
                return element.id !== id ;
        }); 
        setItems(updatedItems)
    }

    const editItem = (id) => {
        setToggle(true);
        const editedItem = items.find((element) =>{
            return element.id === id;
        });
        setInputData(editedItem.name);
        setId(id);
    }

    return(
        <>
        <div className="main-div ">
            <div className="child-div">
                <figure>
                    <img src={img} alt="todoLogo" />
                    <figcaption>Add Your List Here ðŸ¤˜</figcaption>
                </figure>
                <div className=" addItems">
                    <input type="text" value = {inputData}
                    onChange = {(e) =>setInputData(e.target.value)}
                     placeholder ='âœ Add Items...' 
                     onKeyPress = { 
                        (event)=>{ 
                        if(event.key === 'Enter'){ 
                            addItem(); 
                        }} 
                    }/>
                    {
                        toggle ?<i className = 'fas fa-edit add-btn' title = 'Update Item'
                                onClick ={addItem}></i>
                                : <i className = 'fa fa-plus add-btn' title = 'Add Item'
                                onClick ={addItem}></i>
                    }
                </div>
                <div className="showItems">
                   {
                       items.map((item , index) =>{
                        return(
                            <div className="eachItem" key={item.id}>
                                <h3>{item.name}</h3>
                               <div className="todo-btn">
                                    <i className="fas fa-edit add-btn" title='Edit Item'
                                        onClick = {() => editItem(item.id)}></i>
                                    <i className="fas fa-trash-alt add-btn" title='Delete Item' 
                                        onClick = {() => deleteItem(item.id)}></i>
                               </div>
                            </div>
                        )
                       })
                   }
                </div>
                {/* clear all <button></button> */}
                <div className="showItems">
                    <button className = 'btn effect04' data-sm-link-text = 'Remove All'
                    onClick ={ () => {setItems([])} }><span>
                        Check List
                        </span></button>
                </div>
            </div>
        </div>
        </>
    )
}

export default ToDo;